.testimonials-section {
  background: #eee;
  text-align: center;
  position: relative;
  padding: $mobile-section-margin 0;

  @media ($large-screen-up) {
    padding: $desk-section-margin 0;
  }

  .wrap {
    // padding: 6em 0;
    position: relative;
    z-index: 10;

    h2 {
      &:before,
      &:after {
        color: $white;
      }
    }

    .quotes {
      background: url("assets/images/icon_quotes.png") no-repeat center;
      width: 100%;
      height: 30px;
      margin-bottom: 2em;
      background-size: contain;
      display: block;
    }

    .testimonial-title {
      text-transform: uppercase;
      margin-bottom: 0;
      margin-top: 1em;
      display: block;
      color: #000;
    }

    .testimonial {
      *:last-child {
        margin-bottom: 0;
      }
    }
  }

  .left-arrow,
  .right-arrow {
    @include base_transition();
    transform: translateY(-50%);
    display: block !important;
    opacity: 0.5;
    position: absolute;
    top: 50%;
    z-index: 100;
    // margin-top: 2em;

    &.slick-hidden {
      display: none !important;
    }

    svg {
      fill: $dark-gray;
      height: 50px;
      width: 50px;
    }

    &:hover {
      cursor: pointer;
      opacity: 1;
    }
  }

  .left-arrow {
    left: -20px;
    svg {
      transform: rotate(180deg);
    }
  }

  .right-arrow {
    right: -20px;
  }
  .testimonials-feed-bottom {
    margin-top: 1.5em;
    text-align: center;
  }
}
.testimonials-slider {
  display: none;
  padding: 0 1.5em;

  @media ($medium-screen-up) {
    padding: 0 3em;
  }
}
