/**
 * Components
 */
body {
  position: relative;
  overflow-x: hidden;
}
body.locked {
  overflow: hidden;
}

.site-container {
  height: 100%;
  overflow: hidden;
  position: relative;
  right: 0;
  transition: all 500ms ease;
  width: 100%;
}
.site-container.move-left {
  right: 285px;
}

.mobile-menu {
  box-sizing: border-box;
  padding: 30px;
  background-color: #222;
  height: 100%;
  overflow: auto;
  position: fixed;
  right: -300px;
  top: 0;
  width: 300px;
  z-index: 2000;
  transition: all 500ms ease;
  font-size: 1rem;
}
.mobile-menu.move-in {
  right: 0;
}
.admin-bar .mobile-menu {
  margin-top: 32px;
}
@media (max-width: 782px) {
  .admin-bar .mobile-menu {
    margin-top: 46px;
  }
}
.mobile-menu ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.mobile-menu .menu > li {
  margin-left: 0;
}
.mobile-menu .menu > li:before {
  content: none;
}
.mobile-menu .menu > li:last-child {
  border-bottom: 0;
  margin-bottom: 0;
  padding-bottom: 0;
}
.mobile-menu .menu > li > a {
  font-family: "Arial";
  font-weight: bold;
  color: #fff;
  display: block;
  margin-bottom: 1.25em;
  line-height: 1em;
  text-transform: uppercase;
}
.mobile-menu .menu > li.menu-item-has-children {
  position: relative;
}
.mobile-menu .menu > li.menu-item-has-children:after {
  transition: all 500ms ease;
  background-image: url("assets/icons/src/arrow-white.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  cursor: pointer;
  height: 26px;
  position: absolute;
  right: -5px;
  top: -3px;
  transform: rotate(0);
  width: 26px;
}
.mobile-menu .menu > li.menu-item-has-children.active:after {
  transform: rotate(90deg);
}
.mobile-menu .menu > li.menu-item-has-children .sub-menu {
  padding-left: 0.625em;
  display: none;
  margin-bottom: 1.375em;
  margin-top: -10px;
}
.mobile-menu .menu > li.menu-item-has-children .sub-menu li {
  margin-left: 0;
}
.mobile-menu .menu > li.menu-item-has-children .sub-menu li:before {
  content: none;
}
.mobile-menu .menu > li.menu-item-has-children .sub-menu li:first-child {
  margin-top: 0;
}
.mobile-menu .menu > li.menu-item-has-children .sub-menu li:last-child {
  margin-bottom: 0;
}
.mobile-menu .menu > li.menu-item-has-children .sub-menu a {
  color: #eee;
  font-family: "Arial";
}
.mobile-menu .icon-close-container {
  position: absolute;
  width: 28px;
  height: 28px;
  top: 16px;
  right: 14px;
  cursor: pointer;
  z-index: 99999;
}
.mobile-menu .icon-close {
  width: 28px;
  height: 28px;
}
.mobile-menu .icon-close path:first-child {
  fill: #fff;
}

.mobile-menu-search {
  margin-top: 1.875em;
}
.mobile-menu-search input[type=submit] {
  display: none !important;
}

.mobile-menu-icon {
  width: 30px;
  height: 20px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%) rotate(0deg);
  transition: 300ms ease;
  cursor: pointer;
  z-index: 310;
}
@media (min-width: 900px) {
  .mobile-menu-icon {
    display: none;
  }
}

.mobile-menu-icon span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: #222;
  border-radius: 2px;
  left: 0;
  transform: rotate(0deg);
  transition: 150ms ease;
}

.mobile-menu-icon span:nth-child(1) {
  top: 0px;
}

.mobile-menu-icon span:nth-child(2),
.mobile-menu-icon span:nth-child(3) {
  top: 8px;
}

.mobile-menu-icon span:nth-child(4) {
  top: 16px;
}

.site-container.move-left .mobile-menu-icon span:nth-child(1) {
  width: 0%;
  left: 50%;
}

.site-container.move-left .mobile-menu-icon span:nth-child(2) {
  transform: rotate(45deg);
}

.site-container.move-left .mobile-menu-icon span:nth-child(3) {
  transform: rotate(-45deg);
}

.site-container.move-left .mobile-menu-icon span:nth-child(4) {
  width: 0%;
  left: 50%;
}

.alert-bar {
  background: #0b406b;
  color: white;
  text-align: center;
  padding: 1em 0;
}
